/* 键盘图标样式 */
.keyboardIcon{
    /* position: absolute; */
    width: 22px;
    height: 20px;
    -webkit-transition:all 0.3s;
    transition:all 0.3s;
    /* display: none; */
}
.keyboardBord{
    position: absolute;
    right: 12px;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border: 1px solid rgb(221, 221, 221);
    z-index: 11;
    -webkit-transition:top 0.2s;
    transition:top 0.2s;
}
.keyboardBord.On{
    background-color:#000;
    border: 1px solid rgb(000, 000, 000);
    top:2px;
}
.keyboardBord.On:active{
    background-color:#6a6a6a;
    top:2px;
}
.keyboardBord.Off{
    background-color: rgba(255,255,255,0.4);;
    top:58px;
}

