.colorList{
  list-style: none;
  min-height: 8.7rem;
  padding: 32px 10px 0 10px;
  margin: 0;
  display: flex;
  flex-flow: row wrap;
  align-content: flex-start;
}
.colorList li{
  /*width: 24px;*/
  /*height: 24px;*/
  float: left;
  /*margin-left: 40px;*/
  flex: 0 0 20%;
  margin-bottom: 32px;
  position: relative;
}
.colorList li .childModel{
  /*width: 25px;*/
  /*height: 25px;*/
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}

.colorActive{
  /*width: 31px;*/
  /*height: 31px;*/
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  display: block !important;
}

/* 键盘图标样式 */
.keyboardIcon{
    /* position: absolute; */
    width: 22px;
    height: 20px;
    transition:all 0.3s;
    /* display: none; */
}
.keyboardBord{
    position: absolute;
    right: 12px;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgb(221, 221, 221);
    z-index: 11;
    transition:top 0.2s;
}
.keyboardBord.On{
    background-color:#000;
    border: 1px solid rgb(000, 000, 000);
    top:2px;
}
.keyboardBord.On:active{
    background-color:#6a6a6a;
    top:2px;
}
.keyboardBord.Off{
    background-color: rgba(255,255,255,0.4);;
    top:58px;
}
