.jiubox{
    /* width: 97%;
    height:86.2%; */
    position:absolute;
    /* margin:0 auto; */
    display:flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items:center;
    z-index:-1;
    /* background: #eee; */
    border: #ddd 1px solid;
}
.wrapBox{
    transition: all 0.4s;
}
.jiud{
    display:none;
}
.jiuitem {
     /* background:transparent; */
     border: #ddd 1px solid;
     /* width:116px;
     height: 115px; */
}
.imgBackground{
    transition:all 0.4s;
}
.gotoMixIcon{
    transition:all 0.4s;
}
.jiubox .jiuitem:nth-child(3n+2){
    margin-left:-2px;
    margin-right:-2px;
}
.firstVisited{
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  overflow: hidden;
  background-color: #fff;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

